* {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  text-align: center;
}
body {
  margin: 100px;
  background-color: rgb(214, 214, 214);
  transition: 1s;
}
.to-do{
  background-color: rgb(219, 250, 184);
  text-align: center;
  width: 100%;
  min-height: 160px;
  border-radius: 7px;
  padding: 10px 10px;
  overflow-x: scroll;

}
input{
  width:80% ;
  font-size: 17px;
  height: 40px;
  outline: none;
  border-radius:4px ;
  border: 1px solid black;
  box-shadow: 1px  1px 4px black;
}
button{
  width: fit-content;
  border: none;
  box-shadow: 1px  1px 4px black;
  padding: 0px 0px;
  height: 43px;
  margin: 3px;
  border-radius: 4px;
}
ol{
  padding: 0;
}
li{
  display: flex;
  align-items: center;
  margin: 4px;
  border: 1px black solid;
  color: rgb(0, 0, 0);
  background-color: rgb(252, 252, 252);
  border-radius: 4px;
  box-shadow: 1px  1px 4px black;
}
.text{
  flex: 1;
  
}
.delete-but , .move-but{
  padding: 5px 8px;
  font-size: 10px;
}
.add-button{
  background-color: rgb(255, 255, 255);
}
span{
  font-size: small;
}
ol{
  width: fit-content;
  min-width:100% ;
}
h1{
  color: rgb(0, 0, 0);
  border-radius: 30px;
}
header{
  color:black;
  background-color: rgb(219, 250, 184);
  width: 100%;
  margin: 0;
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 0;
  left: 0;
  height: 50px;
  text-align: center;
  flex-grow:1 ;
}
.head-dit{
  margin-top: -35px;
}
.switch-theam{
  background-color: rgba(255, 255, 255, 0);
  margin-left: 5px;
  width: 43px;
  position: relative;
  border: none;
  border-radius: 50px;

}
.dark{
  background-color: rgb(0, 0, 0);
  transition: 1s;
}
  .to-do li:nth-child(odd){
    color: antiquewhite;
    background-color: rgb(78, 110, 158);
  
  }